.exchange-maket > header {
  z-index: 10001;
}

.exchange-maket .filter,
.exchange-maket .transaction {
  width: 0.533333rem;
  height: 0.533333rem;
}

.exchange-maket .filter-board {
  position: absolute;
  width: 100%;
  height: auto !important;
  z-index: 10000;
  display: flex;
  flex-wrap: wrap;
  background: #fff;
  padding-bottom: 0.346667rem;
}

.exchange-maket .filter-board > span {
  width: 33.3%;
  text-align: center;
}

.exchange-maket .filter-board > span .generation-btn {
  background: #f5f5f5;
  color: #616161;
  display: inline-block;
  width: 2.8rem;
  height: 1.066667rem;
  font-size: 0.4rem;
  line-height: 1.066667rem;
  border-radius: 0.053333rem;
  font-weight: 500;
}

.exchange-maket .filter-board > span .generation-btn.active {
  background-image: linear-gradient(-90deg, #B0851E 0%, #F2C961 100%);
  color: #fff;
}

.exchange-maket .filter-modal {
  background: #000;
  opacity: 0.5;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.exchange-maket .exchange {
  position: relative;
}

.exchange-maket .exchange .chart {
  width: 100%;
  height: 3.826667rem;
  margin-top: 0.32rem;
  margin-bottom: 0.32rem;
}

.exchange-maket .exchange .chart .charts {
  width: 100%;
  height: 3.826667rem;
}

.exchange-maket .exchange .chart .charts .highcharts-container {
  width: 100%;
  height: 3.826667rem;
}

.exchange-maket .exchange .chart .charts .highcharts-container .highcharts-xaxis-grid .highcharts-grid-line {
  stroke-width: 0.026667rem;
  stroke: #d8d8d8;
}

.exchange-maket .exchange .chart-title {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #878787;
  letter-spacing: 0;
  line-height: 0.56rem;
  text-align: center;
  margin-bottom: 0.213333rem;
}

.exchange-maket .exchange .content {
  width: 100%;
  height: calc(100% - 3.826667rem - 0.64rem - 0.773333rem);
  left: 0;
  position: relative;
}

.exchange-maket .exchange .content .sell-box {
  background: #f6f7f7;
  position: absolute;
  overflow: scroll;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
}

.exchange-maket .exchange .content .sell-box .order-item {
  margin-bottom: 0.32rem;
}

.exchange-maket .exchange .content .no-order {
  width: 100%;
  height: 100%;
  text-align: center;
}

.exchange-maket .exchange .content .no-order img {
  width: 3.053333rem;
  height: 2rem;
  margin-bottom: 0.32rem;
  margin-top: 25%;
}

.exchange-maket .exchange .content .no-order p {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #545454;
  letter-spacing: 0;
  line-height: 0.56rem;
}


