.account-invite {
  padding-bottom: 0.866667rem;
  text-align: center;
  font-family: PingFangSC-Regular;
  padding: 0 0.426667rem;
}

.account-invite > header {
  font-size: 0.4rem;
  color: #696969;
  margin-top: 0.76rem;
}

.account-invite > header > span {
  color: #F2D479;
}

.account-invite > .tips-1 {
  font-size: 0.32rem;
  color: #9B9B9B;
  margin-top: 0.24rem;
}

.account-invite > .code {
  font-family: PingFangSC-Medium;
  font-size: 0.533333rem;
  color: #222;
  margin-top: 0.573333rem;
}

.account-invite > .mint-button {
  display: block;
  border-radius: 0.64rem;
  margin: 0.506667rem auto 0 auto;
