  height: 1.386667rem;
  line-height: 1.28rem;
  font-family: PingFangSC-Medium;
  font-size: 0.48rem;
  color: #FFFFFF;
}

.account-invite > img {
  display: block;
  width: 4.586667rem;
  height: 4.586667rem;
  border-radius: 0.213333rem;
  margin: 3.106667rem auto 0 auto;
}

.account-invite > .tips-2 {
  font-size: 0.32rem;
  color: #9B9B9B;
  margin-top: 0.56rem;
}


