.account-profile-nickname .tips {
  padding: 0 0.426667rem;
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #545454;
  margin-top: 0.32rem;
}

.account-profile-nickname .radio-fields header {
  font-size: 0.426667rem;
  color: #545454;
  padding: 0 0.426667rem;
  margin-top: 0.426667rem;
}

.account-profile-nickname .radio-fields .content {
  display: flex;
  align-items: center;
  padding: 0 0.426667rem;
  height: 0.666667rem;
  margin-top: 0.213333rem;
  justify-content: space-around;
}

.account-profile-nickname .radio-fields .content > div {
  display: flex;
  align-items: center;
  position: relative;
  height: 0.666667rem;
}

.account-profile-nickname .radio-fields .content > div:last-child {
  margin-right: 0;
}

.account-profile-nickname .radio-fields .content > div input {
  opacity: 0;
  width: 0.533333rem;
  height: 0.533333rem;
  margin: 0;
}

.account-profile-nickname .radio-fields .content > div label {
  width: 0.426667rem;
  height: 0.426667rem;
  position: absolute;
  left: 0;
  border-radius: 50%;
  background-image: url("../../../assets/img/icon/radio.png");
  background-size: 100% 100%;
}

.account-profile-nickname .radio-fields .content > div input:checked + label {
  background-image: url("../../../assets/img/icon/radio_selected.png");
}

.account-profile-nickname .radio-fields .content > div span {
  font-size: 0.4rem;
  color: #000000;
  margin-left: 0.253333rem;
}


