.payment-info {
  margin-top: 0.24rem;
  background-color: #fff;
}

.payment-info > header {
  font-size: 0.373333rem;
  color: #222;
  line-height: 1;
  padding: 0.32rem 0.426667rem;
  border-bottom: 0.066667rem solid #f1f1f1;
}

.payment-info > header.noborder {
  border: 0;
}

.payment-info > .group .item {
  border-bottom: 0.026667rem solid #f1f1f1;
}

.payment-info > .group .item:last-child {
  border: 0;
}

.payment-info > .group .item > header {
  height: 0.973333rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.426667rem;
}

.payment-info > .group .item > header .left {
  display: flex;
  align-items: center;
  position: relative;
}

.payment-info > .group .item > header .left .text {
  display: flex;
  align-items: center;
}

.payment-info > .group .item > header .left .text i {
  width: 0.48rem;
  height: 0.48rem;
}

.payment-info > .group .item > header .left .text span {
  color: #222;
  font-size: 0.4rem;
  margin-left: 0.333333rem;
}

.payment-info > .group .item > header .left input {
  opacity: 0;
  width: 0.36rem;
  height: 0.36rem;
  margin: 0 0.333333rem 0 0;
}

.payment-info > .group .item > header .left label {
  width: 0.36rem;
  height: 0.36rem;
  position: absolute;
  left: 0;
  border-radius: 50%;
  background-image: url("../../../assets/img/icon/radio.png");
  background-size: 100% 100%;
}

.payment-info > .group .item > header .left input:checked + label {
  background-image: url("../../../assets/img/icon/radio_select_green.png");
}

.payment-info > .group .item > header .right {
  display: flex;
  align-items: center;
}

.payment-info > .group .item > header .right img {
  width: 0.32rem;
  transform: rotate(-90deg);
}

.payment-info > .group .item > header .right img.reverse {
  transform: rotate(0deg);
}

.payment-info > .group .item .content {
  padding: 0.266667rem 0.426667rem 0.56rem;
}

.payment-info > .group .item .content > div {
  display: flex;
  align-items: center;
  margin-bottom: 0.346667rem;
  font-size: 0.32rem;
  color: #9B9B9B;
}

.payment-info > .group .item .content > div:last-child {
  margin-bottom: 0;
}

.payment-info > .group .item .content > div span.title {
  width: 2.933333rem;
  text-align: right;
}

.payment-info > .group .item .content > div span.field {
  width: calc(100% - 2.933333rem);
}

.payment-info > .group .item .content > div span.field img {
  width: 0.64rem;
  height: 0.64rem;
}

.payment-info > .group .item .content > div span.field:nth-child(2) {
  width: auto;
  max-width: calc(100% - 3.2rem - 1.8rem);
  word-wrap: break-word;
  overflow: hidden;
}

.payment-info > .group .item .content > div .copy-btn {
  width: 1.6rem;
  font-size: 0.32rem;
  padding: 0;
  height: 0.533333rem;
  line-height: 0.533333rem;
  background-color: #fff;
  border-radius: 0.266667rem;
  margin-left: 0.2rem;
}

.payment-info > .group-paied {
  padding: 0 0.426667rem;
}

.payment-info > .group-paied > header {
  display: flex;
  align-items: center;
  margin-bottom: 0.346667rem;
}

.payment-info > .group-paied > header i {
  width: 0.48rem;
  height: 0.48rem;
}

.payment-info > .group-paied > header span {
  color: #222;
  font-size: 0.4rem;
  margin-left: 0.333333rem;
}

.payment-info > .group-paied > .content {
  padding-bottom: 0.426667rem;
}

.payment-info > .group-paied > .content .copy-btn {
  width: 1.28rem;
  font-size: 0.32rem;
  padding: 0;
  height: 0.533333rem;
  line-height: 0.533333rem;
  background-color: #fff;
  border-radius: 0.266667rem;
  margin-left: 0.2rem;
}

.payment-info > .group-paied > .content > div {
  display: flex;
  align-items: center;
  margin-bottom: 0.346667rem;
  font-size: 0.32rem;
}

.payment-info > .group-paied > .content > div:last-child {
  margin-bottom: 0;
}

.payment-info > .group-paied > .content > div span.title {
  color: #9b9b9b;
  text-align: left;
  width: 40%;
}

.payment-info > .group-paied > .content > div span.field {
  color: #9b9b9b;
  text-align: right;
}

.payment-info > .group-paied > .content > div span.field img {
  width: 0.64rem;
  height: 0.64rem;
}

.payment-info > .group-paied > .content > div span.field:nth-child(2) {
  width: calc(60% - 1.48rem);
  word-wrap: break-word;
}

.payment-info > .group-paied > .content > div span.field:last-child {
  width: 60%;
}


