.order-operation {
  height: 1.173333rem;
}

.order-operation > header {
  position: relative;
  background: linear-gradient(75deg, #13192b, #2b271c, #0d0e0e);
}

.order-operation > header > .mint-button {
  position: absolute;
  left: 0;
  top: 0;
  background: transparent;
  box-shadow: none;
  border: 0;
}

.order-operation > header > .mint-button:active,
.order-operation > header > .mint-button:after {
  background: transparent;
}

.order-operation > header > .mint-button .mint-button-icon i {
  color: #F2D479;
}

.order-operation .mint-navbar {
  height: 1.173333rem;
  background: transparent !important;
}

.order-operation .mint-navbar a {
  padding: 0;
}

.order-operation .mint-navbar a .mint-tab-item-label {
  line-height: 1.173333rem !important;
  color: #F2D479 !important;
}

.order-operation .mint-navbar .mint-tab-item::before {
  background: #F2D479 !important;
  height: 0.12rem !important;
  width: 0.933333rem !important;
}

.order-operation .buy-content {
  margin-top: 7;
}

.order-operation .sell-content {
  margin-top: 0.093333rem;
}

.order-operation .sure {
  display: block;
  width: calc(100% - 0.853333rem);
  height: 1.386667rem;
  margin-top: 1.36rem;
  margin-left: 0.426667rem;
  font-size: 0.48rem;
  border-radius: 0.133333rem;
}


