.count-down {
  height: 100%;
  position: relative;
}

.count-down:after {
  content: '';
  position: absolute;
  height: calc(100% - 0.533333rem);
  left: 0;
  top: 0.266667rem;
  width: 0.026667rem;
  background: #000;
}

.count-down .mint-button {
  height: 100%;
  border: 0 !important;
  font-size: 0.32rem;
}

.count-down .mint-button.send-btn {
  color: #EA2525;
}


