.account-service {
  padding-bottom: 0.866667rem;
  text-align: center;
  font-family: PingFangSC-Regular;
  padding: 0 0.426667rem;
}

.account-service > header {
  font-size: 0.453333rem;
  color: #545454;
  margin-top: 0.853333rem;
}

.account-service > header > span {
  color: #485EF0;
}

.account-service > .tips-1 {
  font-size: 0.4rem;
  color: #000;
  margin-top: 0.253333rem;
}

.account-service > .code {
  font-family: PingFangSC-Medium;
  font-size: 0.853333rem;
  color: #000;
  margin-top: 0.533333rem;
}

.account-service > .mint-button {
  display: block;
  border-radius: 0.64rem;
  margin: 3.546667rem auto 0 auto;
  padding: 0 1.36rem;
  height: 1.28rem;
  line-height: 1.28rem;
  font-family: PingFangSC-Medium;
  font-size: 0.48rem;
  color: #FFFFFF;
}

.account-service > img {
  display: block;
  width: 4.88rem;
  height: 4.88rem;
  border-radius: 0.213333rem;
  margin: 1.493333rem auto 0 auto;
}

.account-service > .tips-2 {
  font-size: 0.32rem;
  color: dimgray;
  margin-top: 0.48rem;
  font-weight: 500;
}


