.order-info {
  padding: 0.426667rem 0.426667rem 0.2rem;
  background-color: #fff;
}

.order-info .order-title {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #222;
  line-height: 1;
  margin-bottom: 0.44rem;
}

.order-info .item-info {
  display: flex;
  margin-bottom: 0.386667rem;
  align-items: center;
}

.order-info .item-info p {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  line-height: 1;
}

.order-info .item-info p.left {
  color: #9b9b9b;
  text-align: left;
  width: 40%;
}

.order-info .item-info p.right {
  width: 60%;
  color: #9b9b9b;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.order-info .item-info p.right > span {
  width: 100%;
}

.order-info .item-info p.right .copy-btn {
  width: 1.6rem;
  font-size: 0.32rem;
  padding: 0;
  height: 0.533333rem;
  line-height: 0.533333rem;
  background-color: #fff;
  border-radius: 0.266667rem;
  margin-left: 0.2rem;
}


