.bank-form-bank {
  width: 100%;
}

.bank-form-bank .add-qr {
  overflow: hidden;
  padding-top: 0.426667rem;
  margin-left: 0.426667rem;
}

.bank-form-bank .add-qr .qr-title {
  font-size: 0.426667rem;
  line-height: 0.6rem;
  color: #545454;
  margin-bottom: 0.426667rem;
}

.bank-form-bank .add-qr .add-icon {
  border: 0.026667rem solid #BABABA;
