.auth-answer .question-fields > header {
  margin-top: 0.773333rem;
  font-family: PingFangSC-Regular;
  font-size: 0.853333rem;
  color: #222;
  padding-left: 0.426667rem;
  margin-bottom: 0.493333rem;
  line-height: 1;
}

.auth-answer .question-fields .mint-radiolist .mint-radiolist-title {
  margin: 0;
  margin-bottom: 0.426667rem;
  padding: 0 0.453333rem;
  font-size: 0.453333rem;
  color: #545454;
}

.auth-answer .question-fields .mint-radiolist .mint-cell {
  margin: 0 0.426667rem 0.32rem;
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper {
  padding-left: 0 !important;
  padding-right: 0 !important;
  background-color: #f1f1f1 !important;
  border-radius: 0.053333rem;
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper .mint-cell-title .mint-radiolist-label {
  display: flex;
  align-items: center;
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper .mint-cell-title .mint-radiolist-label .mint-radio .mint-radio-core {
  background-size: 100%;
  background-image: url("../../assets/img/icon/radio.png");
  border: 0;
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper .mint-cell-title .mint-radiolist-label .mint-radio .mint-radio-core:after {
  display: none !important;
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper .mint-cell-title .mint-radiolist-label .mint-radio .mint-radio-input:checked + .mint-radio-core {
  background-image: url("../../assets/img/icon/radio_selected.png");
}

.auth-answer .question-fields .mint-radiolist .mint-cell .mint-cell-wrapper .mint-cell-title .mint-radiolist-label .mint-radio-label {
  line-height: 0.533333rem;
}

.auth-answer .question-fields .mint-button {
  margin: 2.666667rem 0.426667rem 0;
  display: block;
  width: calc(100% - 0.853333rem);
  box-shadow: none;
  border: 0;
  font-family: PingFangSC-Regular;
  font-size: 0.48rem;
  opacity: 1 !important;
  height: 1.306667rem;
}

.auth-answer .answer-result header {
  text-align: center;
  margin-top: 1.066667rem;
}

.auth-answer .answer-result header i {
  width: 1.733333rem;
  height: 1.733333rem;
  background-image: url("../../assets/img/icon/evaluation_success.png");
}

.auth-answer .answer-result > div {
  padding: 0 1.8rem;
  text-align: center;
}

.auth-answer .answer-result > div:nth-child(2) {
  font-size: 0.533333rem;
  color: #333333;
  margin-top: 0.853333rem;
}

.auth-answer .answer-result > div:nth-child(3) {
  font-size: 0.4rem;
  color: #545454;
  margin-top: 0.32rem;
}

.auth-answer .answer-result .mint-button {
  height: 1.386667rem;
  border-radius: 0.64rem;
  width: 4.266667rem;
  margin: 1.08rem auto 0;
  display: block;
}


