.account-setting .cell-group {
  background-color: #fff;
  margin-bottom: 0.546667rem;
}

.account-setting .cell-group .mint-cell .mint-cell-value span {
  opacity: 0.8;
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #545454;
}

.account-setting .cell-group.mobile .mint-cell-value span {
  font-weight: 500;
  color: #474747;
}

.account-setting > .mint-button {
  display: block;
  margin-top: 4.373333rem;
  width: calc(100% - 0.853333rem);
  margin-left: 0.426667rem;
  opacity: 0.8;
  font-family: PingFangSC-Regular;
  font-size: 0.453333rem;
  color: #FFFFFF;
  font-weight: 500;
  border: 0;
  box-shadow: none;
  height: 1.386667rem;
  line-height: 1.386667rem;
}

.account-setting .line {
  margin-left: 0.2rem;
  border-bottom: 0.026667rem solid #eee;
}


