.detail-header {
  padding: 0.506667rem 0.426667rem 0.213333rem;
  background-color: #fff;
}

.detail-header .title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.533333rem;
  align-items: center;
}

.detail-header .title .title-left {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #222;
  letter-spacing: 0;
  line-height: 0.64rem;
  display: flex;
  align-items: center;
}

.detail-header .title .title-left > div {
  display: flex;
  align-items: center;
}

.detail-header .title .title-left .sell-icon {
  display: inline-block;
  width: 0.16rem;
  height: 0.48rem;
  background-color: #CD941B;
  margin-top: 0.08rem;
  margin-right: 0.266667rem;
}

.detail-header .title .title-left .sell-icon.is-sell {
  background-color: #CD941B;
}

.detail-header .title .title-left i {
  width: 0.48rem;
  height: 0.48rem;
  margin-right: 0.32rem;
}

.detail-header .title .title-left i:first-of-type {
  margin-left: 0.64rem;
}

.detail-header .title .title-right {
  font-size: 0.32rem;
  color: #EA2525;
  letter-spacing: 0;
  text-align: right;
  line-height: 0.56rem;
}

.detail-header .sell-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detail-header .sell-content > div {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  color: #9b9b9b;
  line-height: 1;
  margin-bottom: 0.386667rem;
}

.detail-header .sell-content .left {
  text-align: left;
}

.detail-header .sell-content .right {
  text-align: right;
}

.detail-header .sell-content .right.money-amount {
  font-size: 0.32rem;
  color: #222;
  line-height: 1;
}

.detail-header .money {
  font-size: 0.373333rem;
  line-height: 0.533333rem;
  color: rgba(0, 0, 0, 0.53);
  margin-right: 0.213333rem;
}

.detail-header .money span {
  font-family: PingFangSC-Regular;
  font-size: 0.453333rem;
  color: #545454;
  letter-spacing: 0;
  line-height: 0.64rem;
}


