.proof-img {
  background: #fff;
  padding: 0.426667rem 0 0.32rem 0;
  margin-top: 0.24rem;
}

.proof-img .order-title {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #222;
  line-height: 1;
  margin-bottom: 0.44rem;
  padding: 0 0.426667rem;
}

.proof-img .img-content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 0.213333rem;
}

.proof-img .img-content .img-field {
  display: flex;
  align-items: center;
  width: calc(25% - 0.426667rem);
  height: calc(1.0856rem * 2 - 0.213333rem);
  margin: 0.213333rem 0.213333rem 0 0.213333rem;
}

.proof-img .img-content .img-field img {
  width: 100%;
  height: 100%;
}


