.buy-detail .space-line {
  height: 0.32rem;
  width: 100%;
  background: #F6F7F7;
}

.buy-detail .buy-info {
  padding-bottom: 0.666667rem;
  margin-top: 0.24rem;
}

.buy-detail .buy-info .upload {
  padding: 0.426667rem 0 0.32rem 0;
  background-color: #fff;
  margin-top: 0.24rem;
}

.buy-detail .buy-info .upload .order-title {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #222;
  line-height: 1;
  margin-bottom: 0.44rem;
  padding: 0 0.426667rem;
}

.buy-detail .buy-info .upload .tips {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  color: #878787;
  letter-spacing: 0;
  line-height: 0.44rem;
  margin-bottom: 0.48rem;
  padding: 0 0.426667rem;
}

.buy-detail .buy-info .upload .upload-fields {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 0.96rem;
  padding: 0 0.213333rem;
}

.buy-detail .buy-info .upload .upload-fields .img-field {
  display: flex;
  align-items: center;
  width: calc(25% - 0.426667rem);
  height: calc(1.0856rem * 2 - 0.213333rem);
  margin: 0.213333rem 0.213333rem 0 0.213333rem;
  border-radius: 0.133333rem;
  overflow: hidden;
}

.buy-detail .buy-info .upload .upload-fields .img-field img {
  width: 100%;
  height: 100%;
}

.buy-detail .buy-info .upload-btn {
  display: block;
  width: calc(100% - 0.853333rem);
  margin: 0.4rem 0.426667rem 0 0.426667rem;
}

.buy-detail .buy-info .sell-cancel {
  margin-top: 0.4rem;
}

.buy-detail .sell-confirm {
  margin-top: 0.266667rem;
  padding-bottom: 0.426667rem;
  background-color: #fff;
}

.buy-detail .sell-confirm .confirm-btn {
  display: block;
  width: calc(100% - 0.853333rem);
  margin: 1.28rem 0.426667rem 0 0.426667rem;
}


