.news-container ul li {
  padding: 0.32rem 0.426667rem;
  position: relative;
  border-bottom: 0.013333rem solid #EDEFF3;
}

.news-container ul li .header {
  font-family: PingFangSC-Regular;
  display: flex;
  align-items: center;
}

.news-container ul li .header header {
  font-size: 0.453333rem;
  width: 58%;
  color: #222222;
}

.news-container ul li .header .time {
  font-family: PingFangSC-Regular;
  font-size: 0.346667rem;
  width: 40%;
  color: #474747;
  text-align: right;
}

.news-container ul li .summary {
  font-family: PingFangSC-Regular;
  font-size: 0.426667rem;
  color: #878787;
  margin-top: 0.293333rem;
  margin-bottom: 0.4rem;
  width: 80%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.news-container ul li .detail-btn {
  font-size: 0.346667rem;
  line-height: 0.373333rem;
  color: #EBCE75;
  display: flex;
  justify-content: flex-end;
}

.news-container ul li .detail-btn img {
  width: 0.32rem;
  height: 0.373333rem;
  margin-left: 0.08rem;
}


