.asset-general .asset-total {
  background: #fff;
  padding-bottom: 0.546667rem;
  margin-bottom: 0.32rem;
}

.asset-general .asset-total .total {
  background-image: url("../../assets/img/wallet_bg_balance2x.png");
  background-size: 100% 100%;
  height: 4.48rem;
  margin: 0.533333rem 0.32rem 0;
  padding: 0.6rem 0.64rem 0 0.64rem;
}

.asset-general .asset-total .total header {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #FFF5D6;
  line-height: 1;
}

.asset-general .asset-total .total .amount {
  margin-top: 0.52rem;
  font-family: PingFangSC-Medium;
  font-size: 0.8rem;
  color: #F2D479;
  line-height: 1;
}

.asset-general .asset-total .total .total-bottom {
  display: flex;
  margin-top: 0.466667rem;
}

.asset-general .asset-total .total .total-bottom .total-content {
  width: 50%;
}

.asset-general .asset-total .total .total-bottom .total-content:nth-child(2) {
  padding-left: 0.426667rem;
  border-left: 0.013333rem solid rgba(255, 255, 255, 0.2);
}

.asset-general .asset-total .total .total-bottom .total-content .total-title {
  font-size: 0.32rem;
  color: #FFF5D6;
  text-align: left;
  line-height: 1;
}

.asset-general .asset-total .total .total-bottom .total-content .total-count {
  font-family: PingFangSC-Regular;
  font-size: 0.346667rem;
  color: #F2D479;
  text-align: left;
  line-height: 1;
  margin-top: 0.2rem;
}

.asset-general .operation {
  height: calc(100% - 4.48rem - 0.546667rem - 0.32rem - 0.533333rem);
  display: block;
  background: #fff;
  position: relative;
}

.asset-general .operation .mint-navbar {
  height: 1.413333rem;
  padding: 0 !important;
}

.asset-general .operation .mint-navbar .mint-tab-item {
  padding: 0 0.533333rem;
  width: auto !important;
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #878787;
  letter-spacing: -0.0096rem;
  text-align: left;
}

.asset-general .operation .mint-navbar .mint-tab-item.is-selected {
  margin: 0 !important;
}

.asset-general .operation .mint-navbar .mint-tab-item .mint-tab-item-label {
  height: 1.413333rem;
  line-height: 1.413333rem !important;
}

.asset-general .operation .listBox {
  height: calc(100% - 0.32rem - 1.6rem);
  overflow: scroll;
  position: absolute;
  width: 100%;
  top: 1.6rem;
  left: 0;
}


