  left: 0;
  width: 100%;
  z-index: 100;
}

.addressBox .addresss .mint-navbar .mint-tab-item {
  padding: 0;
  height: 1.16rem;
  line-height: 1.16rem;
}

.addressBox .addresss .mint-navbar .mint-tab-item .mint-tab-item-label {
  height: 1.16rem !important;
  line-height: 1.16rem !important;
}

.addressBox .addresss .mint-navbar .mint-tab-item.is-selected {
  color: #21E3E7;
  border-bottom: 0.04rem solid #21E3E7;
}

.addressBox .addresss .mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
  color: #21E3E7 !important;
