}

.addressBox .addresss .content-box .reice-item {
  background: #fff;
  width: 100%;
  padding: 0.32rem 0.426667rem;
  border-radius: 0.106667rem;
  margin-bottom: 0.32rem;
}

.addressBox .addresss .content-box .reice-item .userId {
  font-size: 0.4rem;
