  justify-content: flex-end;
}

.addressBox .addresss .content-box .reice-item .content > div:last-child img {
  width: 1.333333rem;
  height: 1.333333rem;
}

.addressBox .addresss .content-box .reice-item .content .user-name {
  color: #818181;
  font-size: 0.32rem;
  line-height: 1;
  margin-bottom: 0.56rem;
}

.addressBox .addresss .content-box .reice-item .content .user-address {
  font-size: 0.4rem;
  color: #222;
  line-height: 1;
}


