.record-item {
  display: flex;
  justify-content: space-between;
  padding-top: 0.426667rem;
  padding-left: 0.426667rem;
  background: #fff;
}

.record-item .content {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding-right: 0.426667rem;
  border-bottom: 0.026667rem solid #E6E6E6;
  width: calc(100% + 0.853333rem);
  padding-bottom: 0.266667rem;
  align-items: center;
}

.record-item .content .left {
  display: flex;
  flex-direction: column;
}

.record-item .content .left p {
  font-size: 0.4rem;
  color: #222;
}

.record-item .content .left span {
  margin-top: 0.266667rem;
  font-size: 0.32rem;
  color: #818181;
}

.record-item .right {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.record-item .right p {
  font-size: 0.4rem;
  color: #CD941B;
  text-align: right;
  font-weight: 500;
}

.record-item .right span {
  font-size: 0.373333rem;
  line-height: 0.586667rem;
  color: rgba(255, 255, 255, 0.3);
}


