.icon-setting {
  height: 0.533333rem;
  width: 0.533333rem;
  background-image: url("../../assets/img/icon/setting_gray.png");
}

.account .profile {
  height: 2.733333rem;
  background: linear-gradient(75deg, #13192b, #2b271c, #0d0e0e);
}

.account .profile .mint-cell-wrapper {
  height: 2.733333rem;
  background: linear-gradient(75deg, #13192b, #2b271c, #0d0e0e) !important;
}

.account .profile .mint-cell-allow-right::after {
  border-color: #f2d479 !important;
}

.account .profile .mint-cell-title img.avatar {
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 0.053333rem;
}

.account .profile .mint-cell-title .details {
  margin-left: 0.426667rem;
}

.account .profile .mint-cell-title .details header {
  font-family: PingFangSC-Medium;
  font-size: 0.453333rem;
  color: #f2d479;
}

.account .profile .mint-cell-title .details header .name {
  line-height: 0.64rem;
  margin-bottom: 0.066667rem;
  color: #f2d479;
}

.account .profile .mint-cell-title .details header .uid {
  color: #f2d479;
  font-size: 0.373333rem;
  line-height: 0.533333rem;
}

.account .cell-group {
  background-color: #fff;
  margin-top: 0.386667rem;
}

.account .cell-group.profit .mint-cell-title span {
  margin-left: 0 !important;
}

.account .cell-group .icon {
  width: 0.533333rem;
  height: 0.533333rem;
}

.account .cell-group .icon.icon-notice {
  background-image: url("../../assets/img/icon/notice_blue.png");
}

.account .cell-group .icon.icon-address {
  background-image: url("../../assets/img/icon/address_green.png");
}

.account .cell-group .icon.icon-contact {
  background-image: url("../../assets/img/icon/contact_purple.png");
}

.account .cell-group .icon.icon-invite {
  background-image: url("../../assets/img/icon/invite_orange.png");
}

.account .cell-group .icon.icon-friend {
  background-image: url("../../assets/img/icon/friend_blue.png");
}

.account .cell-group .mint-cell .mint-cell-title span {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #222;
  margin-left: 0.32rem;
}

.account .line {
  margin-left: 0.2rem;
  border-bottom: 0.026667rem solid #eee;
}


