.sr-only {
  position: absolute;
  width: 0.013333rem;
  height: 0.013333rem;
  padding: 0;
  margin: -0.013333rem;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.mintui.mintui-fa {
  display: inline-block;
  font-size: 0.213333rem !important;
  font-family: FontAwesome !important;
  font-size: inherit;
  text-rendering: auto;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#app .mint-popup {
  width: 100%;
}

#app .mint-loadmore-bottom .mint-loadmore-text {
  font-size: 0.506667rem;
}

#app .mint-header.is-fixed {
  z-index: 9999;
}

#app .mint-button {
  background-clip: padding-box;
}

#app .mint-button.mint-button--primary {
  background-image: linear-gradient(-90deg, #B0851E 0%, #F2C961 100%);
  color: #FFFFFF;
}

#app .mint-button.mint-button--primary.is-disabled {
  background: #D7D8DE;
  color: #fff;
}

#app .mint-button.buyBtn.is-disabled {
  border: 0.026667rem solid rgba(255, 255, 255, 0.6);
  color: rgba(255, 255, 255, 0.6);
}

#app .mint-button.white-color {
  color: #fff;
}

#app .mint-navbar {
  padding-left: 0.426667rem;
  padding-right: 0.426667rem;
  width: 100%;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
}

#app .mint-navbar.buy-nav {
  width: 40%;
  background: #FFF;
  margin: 0 auto;
}

#app .mint-navbar.buy-nav .mint-tab-item {
  flex: none;
  -webkit-box-flex: none;
  width: 50%;
}

#app .mint-navbar.buy-nav .mint-tab-item.is-selected {
  border: none;
  position: relative;
}

#app .mint-navbar.buy-nav .mint-tab-item.is-selected::before {
  content: '';
  display: block;
  width: 0.533333rem;
  height: 0.053333rem;
  background: #485EF0;
  position: absolute;
  bottom: 0.066667rem;
  left: 50%;
  transform: translateX(-50%);
}

#app .mint-navbar.buy-nav .mint-tab-item.is-selected .mint-tab-item-label {
  color: #000;
}

#app .mint-navbar.buy-nav .mint-tab-item .mint-tab-item-label {
  font-size: 0.48rem;
  line-height: 0.666667rem;
  color: #878787;
}

#app .mint-navbar::-webkit-scrollbar {
  display: none;
}

#app .mint-navbar.tableItem {
  background: #0B1A3B;
}

#app .mint-navbar.tableItem .mint-tab-item {
  flex: none;
  -webkit-box-flex: none;
  width: 25%;
}

#app .mint-navbar.tableItem .mint-tab-item.is-selected {
  margin-bottom: 0;
}

#app .mint-navbar.tableItem .mint-tab-item.is-selected .mint-tab-item-label {
  color: #108EE9;
}

#app .mint-navbar.tableItem .mint-tab-item .mint-tab-item-label {
  font-size: 0.426667rem;
  line-height: 0.6rem;
  color: rgba(255, 255, 255, 0.5);
}

#app .mint-navbar.transfer-nav-item {
  background: #0B1A3B;
}

#app .mint-navbar.transfer-nav-item .mint-tab-item {
  flex: 1;
  -webkit-box-flex: 1;
}

#app .mint-navbar.transfer-nav-item .mint-tab-item .mint-tab-item-label {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.426667rem;
  line-height: 0.6rem;
}

#app .mint-navbar.transfer-nav-item .mint-tab-item.is-selected {
  border: none;
  position: relative;
}

#app .mint-navbar.transfer-nav-item .mint-tab-item.is-selected::before {
  content: '';
  display: block;
  width: 1.706667rem;
  height: 0.053333rem;
  background: #21E3E7;
  position: absolute;
  bottom: 0.066667rem;
  left: 50%;
  transform: translateX(-50%);
}

#app .mint-navbar.transfer-nav-item .mint-tab-item.is-selected .mint-tab-item-label {
  color: #21E3E7;
}

#app .mint-navbar.wallet-nav-item {
  background: #FFF;
}

#app .mint-navbar.wallet-nav-item .mint-tab-item {
  flex: none;
  -webkit-box-flex: none;
  width: 20%;
}

#app .mint-navbar.wallet-nav-item .mint-tab-item.is-selected {
  border: none;
  position: relative;
}

#app .mint-navbar.wallet-nav-item .mint-tab-item.is-selected::before {
  content: '';
  display: block;
  width: 0.533333rem;
  height: 0.053333rem;
  background: #AA8835;
  position: absolute;
  bottom: 0.066667rem;
  left: 50%;
  transform: translateX(-50%);
}

#app .mint-navbar.wallet-nav-item .mint-tab-item.is-selected .mint-tab-item-label {
  color: #CD941B;
}

#app .mint-navbar.wallet-nav-item .mint-tab-item .mint-tab-item-label {
  font-size: 0.426667rem;
  line-height: 0.6rem;
  color: #B9B9B9;
}

#app .mint-cell {
  position: relative;
  background-image: none !important;
}

#app .mint-cell.popup-input .mint-cell-wrapper {
  width: 100%;
  height: 100%;
  border-bottom: 0.013333rem solid #ccc;
}

#app .mint-cell.popup-input .mint-cell-wrapper .mint-cell-title {
  width: 0;
}

#app .mint-cell.popup-input .mint-cell-wrapper .mint-cell-value {
  width: 100%;
  height: 100%;
}

#app .mint-cell.popup-input .mint-cell-wrapper .mint-cell-value .mint-field-core {
  width: 100%;
  height: 100%;
  text-align: center;
}

#app .mint-cell.popup-cell {
  height: 1.306667rem;
}

#app .mint-cell.popup-cell .mint-cell-wrapper {
  background-color: #fff;
  border-bottom: 0.04rem solid rgba(255, 255, 255, 0.3);
  padding-bottom: 0.013333rem;
}

#app .mint-cell.popup-cell .mint-cell-wrapper .mint-cell-title {
  text-align: center;
}

#app .mint-cell.popup-cell .mint-cell-wrapper .mint-cell-title span.mint-cell-text {
  color: #333;
  font-size: 0.4rem;
  line-height: 0.666667rem;
  display: block;
  margin: 0 auto;
}

#app .mint-cell .mint-cell-wrapper {
  padding: 0.293333rem 0.426667rem;
  background: #fff;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-title {
  display: flex;
  align-items: center;
  color: #545454;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-title .mint-cell-text {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #545454;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-value {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #545454;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-value input {
  color: #000;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-value input::-webkit-input-placeholder {
  color: #ccc;
  font-size: 0.4rem;
  line-height: 0.64rem;
}

#app .mint-cell .mint-cell-wrapper .mint-cell-value .mintui-field-error {
  color: #000;
}

#app .mint-cell.detail-input:after {
  display: none;
}

#app .mint-cell.detail-input .mint-cell-wrapper {
  display: block;
  padding: 0 0 0;
}

#app .mint-cell.detail-input .mint-cell-wrapper > .mint-cell-title {
  width: 100%;
}

#app .mint-cell.detail-input .mint-cell-wrapper > .mint-cell-title span.mint-cell-text {
  font-size: 0.4rem;
  line-height: 0.56rem;
}

#app .mint-cell.detail-input .mint-cell-wrapper .mint-cell-value {
  height: 0.96rem;
  background: #F6F7F7;
  border-radius: 0.106667rem;
}

#app .mint-cell.detail-input .mint-cell-wrapper .mint-cell-value .mint-field-core {
  height: 100%;
  background: transparent;
  padding-left: 0.346667rem;
  font-size: 0.4rem;
  line-height: 0.56rem;
  color: #545454;
}

#app .mint-cell.detail-input .mint-cell-wrapper .mint-cell-value .mint-field-core::-webkit-input-placeholder {
  color: #ccc;
  font-size: 0.4rem;
  line-height: 0.56rem;
}

#app .mint-cell.common-input:after {
  display: none;
}

#app .mint-cell.common-input .mint-cell-wrapper {
  display: block;
  padding: 0.426667rem 0.426667rem 0;
}

#app .mint-cell.common-input .mint-cell-wrapper > .mint-cell-title {
  margin-bottom: 0.213333rem;
  width: 100%;
}

#app .mint-cell.common-input .mint-cell-wrapper > .mint-cell-title span.mint-cell-text {
  font-size: 0.4rem;
  line-height: 0.6rem;
  color: #474747;
}

#app .mint-cell.common-input .mint-cell-wrapper .mint-cell-value {
  height: 1.146667rem;
  background: #f1f1f1;
  border-radius: 0.106667rem;
}

#app .mint-cell.common-input .mint-cell-wrapper .mint-cell-value .mint-field-core {
  height: 100%;
  background: transparent;
  padding-left: 0.346667rem;
  color: #000;
}

#app .mint-cell.common-input .mint-cell-wrapper .mint-cell-value .mint-field-core::-webkit-input-placeholder {
  color: #ccc;
  font-size: 0.4rem;
  line-height: 0.64rem;
}

#app .mint-cell:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 0.026667rem;
  width: 8.346667rem;
  background-color: rgba(255, 255, 255, 0.1);
}

#app .mint-cell:first-child:after {
  display: none;
}

.mint-toast {
  opacity: 0.9;
  background: #000000 !important;
  padding: 0.56rem !important;
  border-radius: 0.16rem !important;
}

.mint-toast .mint-toast-icon.icon.icon-success-check {
  width: 1.28rem;
  height: 1.28rem;
  background-image: url("~@/../../src/app/assets/img/icon/success.png");
}

.mint-toast .mint-toast-icon.icon.icon-error-x {
  width: 1.28rem;
  height: 1.28rem;
  background-image: url("~@/../../src/app/assets/img/icon/error.png");
}

.mint-toast .mint-toast-text {
  padding: 0 !important;
  margin-top: 0.133333rem !important;
}

.mint-indicator .mint-indicator-wrapper {
  height: 2.4rem;
