.home {
  background-image: url("../assets/img/bg/bg_index.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
  position: relative;
}

.home .mint-button {
  position: absolute;
  box-shadow: none;
  border: 0;
}

.home .mint-button.lang-btn {
  top: 0.893333rem;
  right: 0.426667rem;
  padding: 0;
  height: auto;
  background: transparent;
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #F2D479;
}

.home .mint-button.signin-btn {
  box-shadow: 0.013333rem 0.026667rem 0.066667rem 0.013333rem rgba(242, 201, 97, 0.5);
  font-family: PingFangSC-Medium;
  font-size: 0.48rem;
  color: #FFFFFF;
  width: calc(100% - 3.2rem);
  left: 1.6rem;
  bottom: 2.693333rem;
  height: 1.28rem;
  border-radius: 1.28rem;
  background: #D8AE68 !important;
}

.home .tips {
  position: absolute;
  font-size: 0.346667rem;
  text-align: center;
  color: #8B8EA0;
  line-height: 0.48rem;
  bottom: 1.48rem;
  left: 0.64rem;
  width: calc(100% - 1.28rem);
}

.home .tips span {
  color: #0078F0;
}


