.mobile-input {
  position: relative;
}

.mobile-input .picker-btn {
  text-align: right;
  height: 1.066667rem;
  line-height: 1.066667rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.mobile-input .picker-btn span {
  font-size: 0.4rem;
  padding: 0 0.066667rem;
}

.mobile-input input {
  padding-left: 2.2rem !important;
}

.mobile-input .country-select {
  position: absolute;
  bottom: 0;
  left: 0.426667rem;
  padding-left: 0.333333rem;
  padding-right: 0.346667rem;
  height: 1.146667rem;
  background: transparent;
  box-shadow: none;
  border: 0;
  width: 1.866667rem;
}

.mobile-input .country-select:active,
.mobile-input .country-select:after {
  background: transparent;
}

.mobile-input .country-select:before {
  position: absolute;
  content: '';
  width: 0.026667rem;
  height: 0.533333rem;
  background: #444053;
  right: 0;
  top: 0.306667rem;
}

.mobile-input .country-select label {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mobile-input .country-select span {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #000;
}

.mobile-input .country-select i {
  font-size: 0.32rem !important;
  color: #000;
}


