.cust-container > header {
  z-index: 10001;
}

.filter-btn {
  font-size: 0.373333rem;
  color: #F2D479;
}

.filter-board {
  position: absolute;
  width: 100%;
  height: auto !important;
  z-index: 10000;
  display: flex;
  flex-wrap: wrap;
  background: #fff;
  padding-bottom: 0.346667rem;
}

.filter-board > span {
  width: 33.3%;
  text-align: center;
}

.filter-board > span .generation-btn {
  background: #f1f1f1;
  color: #696969;
  display: inline-block;
  width: 2.8rem;
  height: 1.066667rem;
  font-size: 0.4rem;
  line-height: 1.066667rem;
  border-radius: 0.106667rem;
}

.filter-board > span .generation-btn.active {
  background: linear-gradient(75deg, #b0851e, #f2c961);
  color: #fff;
}

.filter-modal {
  background: #000;
  opacity: 0.5;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.account-friend {
  padding: 1.2rem 0 0.866667rem 0;
}

.account-friend > header {
  position: fixed;
  top: 1.2rem;
  width: 100%;
  padding: 0.133333rem 0.426667rem;
  left: 0;
  height: 1.2rem;
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  background: #F6F7F7;
  z-index: 9999;
  display: flex;
  align-items: center;
}

.account-friend > header > div {
  color: #222;
  height: 0.533333rem;
  line-height: 0.533333rem;
  margin-bottom: 0.133333rem;
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 0.373333rem;
  justify-content: space-between;
}

.account-friend > header > div:last-child {
  margin-bottom: 0;
}

.account-friend .mint-cell .mint-cell-wrapper {
  padding: 0.426667rem !important;
  align-items: start !important;
}

.account-friend .mint-cell .mint-cell-wrapper .mint-cell-title {
  color: #000;
  align-items: center !important;
}

.account-friend .mint-cell .mint-cell-wrapper .mint-cell-value {
  color: #545454 !important;
}

.account-friend .mint-cell .mint-cell-wrapper .mint-cell-value > span {
  line-height: 0.586667rem;
}

.account-friend .line {
  border-bottom: 0.026667rem solid #E6E6E6;
  margin-left: 0.426667rem;
}

.account-friend .icon {
  width: 0.586667rem;
  height: 0.586667rem;
}

.account-friend .icon.icon-person-blue {
  background-image: url("../../assets/img/icon_person_blue.png");
}

.account-friend .phone_time {
  padding-left: 0.24rem;
}

.account-friend .phone_time > div:first-child {
  color: #222;
  font-size: 0.4rem;
}

.account-friend .phone_time > div:last-child {
  margin-top: 0.4rem;
  color: #696969;
  font-size: 0.346667rem;
}

.filter-fade-enter-active,
.filter-fade-leave-active {
  transition: all 0.5s;
  transform: translateY(0%);
}

.filter-fade-enter,
.filter-fade-leave-to {
  transform: translateY(-100%);
}


