.asset-exchange .asset-total {
  background: #fff;
  padding-bottom: 0.546667rem;
  margin-bottom: 0.32rem;
}

.asset-exchange .asset-total .total {
  background-image: url("../../assets/img/wallet_bg_balance2x.png");
  background-size: 100% 100%;
  height: 4.48rem;
  margin: 0.533333rem 0.32rem 0;
  padding: 0.6rem 0.64rem 0 0.64rem;
}

.asset-exchange .asset-total .total header {
  font-family: PingFangSC-Regular;
  font-size: 0.373333rem;
  color: #FFF5D6;
  line-height: 1;
}

.asset-exchange .asset-total .total .amount {
  margin-top: 0.52rem;
  font-family: PingFangSC-Medium;
  font-size: 0.8rem;
  color: #F2D479;
  line-height: 1;
}

.asset-exchange .asset-total .total .total-bottom .mint-button {
  background-clip: padding-box;
  background-image: linear-gradient(-90deg, #614911 0%, #AA8835 100%);
  border-radius: 0.406667rem;
  color: #fff;
  font-size: 0.373333rem;
  padding: 0 0.573333rem;
  height: 0.746667rem;
  line-height: 0.746667rem;
  border: 0;
}

.asset-exchange .asset-total .total .total-bottom .mint-button:first-child {
  margin-right: 0.32rem;
}

.asset-exchange .operation {
  height: calc(100% - 4.48rem - 0.546667rem - 0.32rem - 0.533333rem);
  display: block;
  background: #fff;
  position: relative;
}

.asset-exchange .operation .mint-navbar {
  height: 1.413333rem;
  padding: 0 !important;
}

.asset-exchange .operation .mint-navbar .mint-tab-item {
  padding: 0 0.533333rem;
  width: auto !important;
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #878787;
  letter-spacing: -0.0096rem;
  text-align: left;
}

.asset-exchange .operation .mint-navbar .mint-tab-item.is-selected {
  margin: 0 !important;
}

.asset-exchange .operation .mint-navbar .mint-tab-item .mint-tab-item-label {
  height: 1.413333rem;
  line-height: 1.413333rem !important;
}

.asset-exchange .operation .listBox {
  height: calc(100% - 0.32rem - 1.6rem - 0.76rem);
  overflow: scroll;
  position: absolute;
  width: 100%;
  top: 1.6rem;
  left: 0;
}


