.password-box {
  position: relative;
}

.password-box .forget-password {
  font-size: 0.32rem;
  line-height: 0.493333rem;
  color: #EA2525;
  position: absolute;
  top: 0.48rem;
  right: 0.426667rem;
}

.password-box .password-input .mint-field-other {
  height: 0.64rem;
}

.password-box .password-input i.icon {
  width: 0.64rem;
  height: 0.64rem;
  margin-right: 0.2rem;
}

.password-box .password-input i.icon-invisible {
  background-image: url("../../assets/img/icon/invisible.png");
}

.password-box .password-input i.icon-visible {
  background-image: url("../../assets/img/icon/visible.png");
}


