.auth-profile .mint-cell .mint-cell-wrapper {
  background: transparent !important;
}

.auth-profile > header {
  margin-top: 0.773333rem;
  font-family: PingFangSC-Regular;
  font-size: 0.853333rem;
  color: #222;
  padding-left: 0.426667rem;
  margin-bottom: 0.493333rem;
  line-height: 1;
}

.auth-profile .radio-fields header {
  font-size: 0.426667rem;
  color: #545454;
  padding: 0 0.426667rem;
  margin-top: 0.426667rem;
}

.auth-profile .radio-fields .content {
  display: flex;
  align-items: center;
  padding: 0 0.426667rem;
  height: 0.666667rem;
  margin-top: 0.213333rem;
  justify-content: space-around;
}

.auth-profile .radio-fields .content > div {
  display: flex;
  align-items: center;
  position: relative;
  height: 0.666667rem;
}

.auth-profile .radio-fields .content > div:last-child {
  margin-right: 0;
}

.auth-profile .radio-fields .content > div input {
  opacity: 0;
  width: 0.533333rem;
  height: 0.533333rem;
  margin: 0;
}

.auth-profile .radio-fields .content > div label {
  width: 0.426667rem;
  height: 0.426667rem;
  position: absolute;
  left: 0;
  border-radius: 50%;
  background-image: url("../../assets/img/icon/radio.png");
  background-size: 100% 100%;
}

.auth-profile .radio-fields .content > div input:checked + label {
  background-image: url("../../assets/img/icon/radio_selected.png");
}

.auth-profile .radio-fields .content > div span {
  font-size: 0.4rem;
  color: #000000;
  margin-left: 0.253333rem;
}

.auth-profile > .mint-button {
  margin: 0 0.426667rem;
  display: block;
  width: calc(100% - 0.853333rem);
  box-shadow: none;
  border: 0;
  font-family: PingFangSC-Regular;
  font-size: 0.48rem;
  margin-top: 1.08rem;
  opacity: 1 !important;
  height: 1.386667rem;
}


