.sell-item {
  padding: 0.506667rem 0.426667rem 0.426667rem;
  background-color: #fff;
}

.sell-item .title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.533333rem;
}

.sell-item .title .title-left {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #222;
  line-height: 1;
  display: flex;
  align-items: center;
}

.sell-item .title .title-left > div {
  display: flex;
  align-items: center;
}

.sell-item .title .title-left .sell-icon {
  display: inline-block;
  width: 0.16rem;
  height: 0.48rem;
  background-color: #CD941B;
  margin-right: 0.32rem;
}

.sell-item .title .title-left .sell-icon.is-sell {
  background-color: #CD941B;
}

.sell-item .title .title-left img {
  width: 0.48rem;
  height: 0.48rem;
  margin-right: 0.32rem;
}

.sell-item .title .title-left img:first-of-type {
  margin-left: 0.64rem;
}

.sell-item .title .title-right {
  display: flex;
  align-items: center;
  font-size: 0.4rem;
  line-height: 1;
  color: #818181;
}

.sell-item .title .title-right img {
  width: 0.586667rem;
  height: 0.586667rem;
}

.sell-item .sell-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.106667rem;
}

.sell-item .sell-content .count {
  font-size: 0.32rem;
  line-height: 1;
  color: #818181;
  margin-right: 0.213333rem;
}

.sell-item .sell-content .count span {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #222;
  line-height: 1;
}

.sell-item .sell-content .count:last-child span {
  color: #CD941B;
}

.sell-item .money {
  font-size: 0.32rem;
  line-height: 1;
  color: #818181;
  margin-right: 0.213333rem;
  margin-top: 0.4rem;
}

.sell-item .money span {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #222;
  font-weight: bold;
  line-height: 1;
}


