.bank-form-alipay {
  width: 100%;
}

.bank-form-alipay .add-qr {
  overflow: hidden;
  padding-top: 0.426667rem;
  margin-left: 0.426667rem;
}

.bank-form-alipay .add-qr .qr-title {
  font-size: 0.426667rem;
  line-height: 1;
  color: #222;
  margin-bottom: 0.253333rem;
}

.bank-form-alipay .add-qr .add-icon {
  border-radius: 0.106667rem;
  width: 1.6rem;
  height: 1.6rem;
}

.bank-form-alipay .add-qr .add-icon img {
  width: 100%;
  height: 100%;
}

.bank-form-alipay .sure-btn {
  display: block;
  width: calc(100% - 0.853333rem);
  margin: 1.933333rem auto 0;
  height: 1.386667rem;
}


