.quotes {
  position: relative;
}

.quotes-container ul li {
  display: flex;
  align-items: center;
  padding: 0.533333rem 0.426667rem;
  width: 100%;
  position: relative;
}

.quotes-container ul li.header {
  font-family: PingFangSC-Regular;
  font-size: 0.293333rem;
  color: #878787;
  padding: 0.346667rem 0.426667rem;
  background: #fff;
  border-bottom: 0.026667rem solid #E6E6E6;
}

.quotes-container ul li.header:after {
  display: none;
}

.quotes-container ul li.header > div {
  font-size: 0.346667rem;
  color: #878787;
  letter-spacing: 0;
  text-align: left;
}

.quotes-container ul li:after {
  position: absolute;
  content: '';
  width: calc(100% - 0.426667rem);
  height: 0.026667rem;
  background: #E6E6E6;
  bottom: 0;
  right: 0;
}

.quotes-container ul li div:first-child {
  width: calc(32% - 0.2rem);
  margin-right: 0.2rem;
}

.quotes-container ul li div:nth-child(2) {
  width: calc(48% - 0.2rem);
  margin-right: 0.2rem;
}

.quotes-container ul li div:last-child {
  width: 20%;
}

.quotes-container ul li.crypto {
  background: #fff;
}

.quotes-container ul li.crypto i {
  width: 0.853333rem;
  height: 0.853333rem;
}

.quotes-container ul li.crypto i.BTC {
  background-image: url("../../assets/img/crypto/BTC.png");
}

.quotes-container ul li.crypto i.ETH {
  background-image: url("../../assets/img/crypto/ETH.png");
}

.quotes-container ul li.crypto i.DASH {
  background-image: url("../../assets/img/crypto/DASH.png");
}

.quotes-container ul li.crypto i.LTC {
  background-image: url("../../assets/img/crypto/LTC.png");
}

.quotes-container ul li.crypto i.USDT {
  background-image: url("../../assets/img/crypto/USDT.png");
}

.quotes-container ul li.crypto i.BSV {
  background-image: url("../../assets/img/crypto/BSV.png");
}

.quotes-container ul li.crypto i.ETC {
  background-image: url("../../assets/img/crypto/ETC.png");
}

.quotes-container ul li.crypto i.EOS {
  background-image: url("../../assets/img/crypto/EOS.png");
}

.quotes-container ul li.crypto i.XRP {
  background-image: url("../../assets/img/crypto/XRP.png");
}

.quotes-container ul li.crypto i.DOGE {
  background-image: url("../../assets/img/crypto/DOGE.png");
}

.quotes-container ul li.crypto div:first-child {
  display: flex;
  align-items: center;
}

.quotes-container ul li.crypto div:first-child span {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #222;
  margin-left: 0.213333rem;
}

.quotes-container ul li.crypto div:nth-child(2) {
  font-size: 0.4rem;
  color: #222;
  letter-spacing: 0;
  text-align: left;
  font-family: SFCompactDisplay-Regular;
}

.quotes-container ul li.crypto div:last-child {
  height: 0.64rem;
  line-height: 0.64rem;
  text-align: center;
  border-radius: 0.106667rem;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: bold;
  color: #FFFFFF;
}

.quotes-container ul li.crypto div:last-child.up {
  background: #4AC76E;
}

.quotes-container ul li.crypto div:last-child.down {
  background: #EA2524;
}


