@charset "UTF-8";

.finance .home-icon {
  width: 100%;
  height: 7.733333rem;
  background: url("../../assets/img/icon/index_bg.png") no-repeat;
  background-size: contain;
  font-family: PingFangSC-Medium;
  text-align: center;
  padding-top: 1.6rem;
}

.finance .home-icon .user-icon {
  width: 2.133333rem;
  height: 2.133333rem;
  border: 0.066667rem solid white;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  display: flex;
  margin-bottom: 0.24rem;
}

.finance .home-icon .user-icon img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.finance .home-icon .user-nick {
  font-size: 0.453333rem;
  color: #fff;
  margin-bottom: 0.08rem;
}

.finance .home-icon .user-uid {
  font-size: 0.293333rem;
  color: #555555;
  text-align: center;
}

.finance .home-icon .user-uid > span {
  display: inline-block;
  background: #E9E9E9;
  height: 0.6rem;
  line-height: 0.6rem;
  border-radius: 0.6rem;
  padding: 0 0.4rem;
}

.finance .content-box {
  padding: 0 0.426667rem;
}

.finance .content-box .total-assets-box {
  width: 100%;
  margin-top: -1.933333rem;
  border-radius: 0.266667rem;
}

.finance .content-box .total-assets-box .total-assets {
  width: 100%;
  height: 4.133333rem;
  padding: 0.4rem 0.32rem 0;
  background: white;
  box-shadow: 0 0 0.266667rem 0 rgba(160, 118, 148, 0.35);
  border-radius: 0.266667rem;
  margin-bottom: 0.266667rem;
}

.finance .content-box .total-assets-box .total-assets .assets-count {
  display: flex;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-left {
  flex: 1;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-left .count-title {
  font-family: PingFangSC-Regular;
  font-size: 0.333333rem;
  color: #66707E;
  margin-bottom: 0.066667rem;
  margin-top: 0.106667rem;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-left .count-title img {
  width: 0.506667rem;
  height: 0.506667rem;
  margin-right: 0.133333rem;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-left .count {
  font-family: PingFangSC-Regular;
  font-size: 0.533333rem;
  color: #0D0D0D;
  letter-spacing: 0;
  line-height: 0.933333rem;
  font-weight: bold;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-right {
  width: 3.066667rem;
  margin-right: 0;
  position: relative;
  top: -0.32rem;
  left: 0.64rem;
}

.finance .content-box .total-assets-box .total-assets .assets-count .count-right img {
  width: 100%;
  height: 100%;
}

.finance .content-box .total-assets-box .total-assets .count-bottom {
  display: flex;
  padding: 0.266667rem 0;
  background: #F0F2F4;
  position: relative;
  top: -0.4rem;
  border-radius: 0.266667rem;
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item {
  width: 50%;
  height: 100%;
  text-align: center;
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item:first-child {
  border-right: 0.013333rem solid #DFE0E4;
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item .title {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  color: #00002C;
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item .title .icon {
  height: 0.32rem;
  width: 0.32rem;
  background: url("../../assets/img/icon/VIP.png") no-repeat;
  background-size: contain;
  display: inline-block;
  margin-right: 0.106667rem;
  position: relative;
  top: 0.013333rem;
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item .title .icon-profit {
  background-image: url("../../assets/img/icon/shouyi.png");
}

.finance .content-box .total-assets-box .total-assets .count-bottom .bottom-item .number {
  font-family: PingFangSC-Regular;
  font-size: 0.453333rem;
  font-weight: bold;
  color: #5B636D;
}

.finance .content-box .total-assets-box .notice-swiper {
  height: 1.066667rem;
  border-radius: 1.066667rem;
  background: white;
  box-shadow: 0 0 0.266667rem 0 rgba(160, 118, 148, 0.35);
}

.finance .content-box .total-assets-box .notice-swiper swiperSlide {
  height: 100%;
}

.finance .content-box .total-assets-box .notice-swiper .notice {
  font-size: 0.373333rem;
  padding: 0.266667rem;
  line-height: 0.533333rem;
  overflow: hidden;
  display: flex;
}

.finance .content-box .total-assets-box .notice-swiper .notice span {
  word-break: keep-all;
  /*  */
  white-space: nowrap;
  /*  */
  overflow: hidden;
  /*  */
  text-overflow: ellipsis;
  color: #A7ABB8;
}

.finance .content-box .total-assets-box .notice-swiper .notice img {
  width: 0.533333rem;
  height: 0.533333rem;
  margin-right: 0.133333rem;
}

.finance .content-box .total-assets-box .notice-swiper .notice .notice-title {
  color: #D4A55D;
  font-size: 0.466667rem;
  display: inline-block;
  width: 1.6rem;
  padding-right: 0.133333rem;
  font-weight: bold;
  font-style: italic;
  border-right: 0.013333rem solid #A7ABB8;
  margin-right: 0.2rem;
}

.finance .content-box .total-assets-box .wallet-box {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 0.533333rem;
}

.finance .content-box .total-assets-box .wallet-box .wallet-item {
  width: calc(33% - 0.213333rem);
  height: 100%;
  box-shadow: 0 0 0.266667rem 0 rgba(160, 118, 148, 0.35);
  border-radius: 0.266667rem;
  padding: 0.266667rem;
  color: white;
  background: url("../../assets/img/icon/money_bg1.png") no-repeat center center;
}

.finance .content-box .total-assets-box .wallet-box .wallet-item .count-title {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  color: #FEFFFF;
}

.finance .content-box .total-assets-box .wallet-box .wallet-item .count {
  font-family: PingFangSC-Regular;
  font-size: 0.426667rem;
  font-weight: bold;
  line-height: 0.8rem;
  color: #FEFFFF;
}

.finance .content-box .total-assets-box .wallet-box .wallet-item:nth-child(2) {
  background-image: url("../../assets/img/icon/money_bg2.png");
}

.finance .content-box .total-assets-box .wallet-box .wallet-item:nth-child(3) {
  background-image: url("../../assets/img/icon/money_bg3.png");
}

.finance .content-box .devlop-box {
  font-family: PingFangSC-Regular;
  font-size: 0.426667rem;
  line-height: 0.6rem;
  color: #878787;
  align-items: center;
  margin-top: 0.533333rem;
  margin-bottom: 0.44rem;
  background: white;
  border-radius: 0.666667rem;
  padding: 0.4rem;
  padding-bottom: 2.133333rem;
  background: url("../../assets/img/icon/develop_bg.png") no-repeat;
  background-position: right bottom;
  background-size: contain;
  box-shadow: 0 0 0.266667rem 0 rgba(160, 118, 148, 0.35);
}

.finance .content-box .devlop-box .devlop-ing {
  text-align: center;
  width: 100%;
  font-weight: bold;
}

.finance .content-box .devlop-box .devlop-ing .devlop-icon {
  display: inline-block;
  background: url("../../assets/img/icon/devlop_left.png") no-repeat;
  width: 0.266667rem;
  height: 0.4rem;
  background-size: contain;
}

.finance .content-box .devlop-box .devlop-ing .devlop-left {
  display: inline-block;
}

.finance .content-box .devlop-box .devlop-ing .devlop-right {
  background-image: url("../../assets/img/icon/devlop_right.png");
}

.finance .content-box .devlop-box .devlop-ing .devlop-text {
  position: relative;
  top: -0.04rem;
}

.finance .content-box .devlop-box .development {
  clear: both;
  content: " ";
  display: block;
  overflow: hidden;
  text-align: center;
  width: 100%;
}

.finance .content-box .devlop-box .development .develop-item {
  float: left;
  border-radius: 0.133333rem;
  font-size: 0.293333rem;
  width: calc((100% / 3) - (0.266667rem * 0.7));
  height: 2.666667rem;
  line-height: 1.4rem;
  background: #FFF7ED;
}

.finance .content-box .devlop-box .development .develop-item img {
  width: 0.933333rem;
  position: relative;
  top: 0.333333rem;
}

.finance .content-box .devlop-box .development .develop-item:nth-child(2),
.finance .content-box .devlop-box .development .develop-item:nth-child(5),
.finance .content-box .devlop-box .development .develop-item:nth-child(8),
.finance .content-box .devlop-box .development .develop-item:nth-child(11) {
  margin: 0.266667rem 0.266667rem 0;
}

.finance .content-box .devlop-box .development .develop-item:nth-child(2) img,
.finance .content-box .devlop-box .development .develop-item:nth-child(5) img,
.finance .content-box .devlop-box .development .develop-item:nth-child(6) img {
  width: 1.066667rem;
}

.finance .content-box .devlop-box .development .develop-item:nth-child(7) img {
  width: 1.2rem;
}

.finance .content-box .devlop-box .development .develop-item:nth-child(3) img,
.finance .content-box .devlop-box .development .develop-item:nth-child(11) img {
  width: 0.8rem;
}

.finance .content-box .devlop-box .development .develop-item {
  margin-top: 0.266667rem;
}

.finance .investTip {
  margin-bottom: 0.426667rem;
}

.finance .profit {
  margin-top: 0.426667rem;
  margin-bottom: 0.426667rem;
}


