.main-container {
  height: 100%;
  position: relative;
}

.main-container .mint-header {
  height: 1.2rem;
  padding: 0 0.426667rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(75deg, #13192b, #2b271c, #0d0e0e);
}

.main-container .mint-header .mint-header-button.is-left .mintui {
  color: #f2d479;
}

.main-container .mint-header .mint-header-button.is-left .mint-button-text {
  font-family: PingFangSC-Regular;
  font-size: 0.426667rem;
  color: #f2d479;
}

.main-container .mint-header .mint-header-button.is-left .mint-button {
  display: flex;
  align-items: center;
}

.main-container .mint-header .mint-header-title {
  font-family: PingFangSC-Medium;
  font-size: 0.48rem;
  color: #f2d479;
  font-weight: 500;
  height: 1.2rem;
  line-height: 1.2rem;
}

.main-container .mint-header .mint-header-button.is-right .mint-button .mint-button-text {
  font-family: PingFangSC-Regular;
  font-size: 0.4rem;
  color: #f2d479;
}

.main-container .mint-header .mint-header-button.is-right .mint-button.is-disabled .mint-button-text {
  color: #ccc;
}

.main-container .container {
  padding-top: 1.173333rem;
  height: 100%;
  padding-bottom: 1.333333rem;
  background-color: #fff;
}

.main-container .container.no-header {
  padding-top: 0;
}

.main-container .container.no-tabbar {
  padding-bottom: 0;
}

.main-container .container > div {
  height: 100%;
  overflow: auto;
}

.main-container .mint-tabbar {
  height: 1.333333rem;
  position: absolute;
  bottom: 0;
  left: 0;
  background: white;
  box-shadow: 0.04rem -0.04rem 0.386667rem 0 rgba(191, 191, 214, 0.3);
}

.main-container .mint-tabbar .mint-tab-item {
  padding: 0;
}

.main-container .mint-tabbar .mint-tab-item .mint-tab-item-icon {
  width: 0.48rem;
  height: 0.533333rem;
  margin-top: 0.186667rem;
}

.main-container .mint-tabbar .mint-tab-item .mint-tab-item-label {
  font-size: 0.293333rem;
  color: #BABABA;
}

.main-container .mint-tabbar .mint-tab-item.is-selected {
  background: #fff;
}

.main-container .mint-tabbar .mint-tab-item.is-selected .mint-tab-item-label {
  color: #CD941B;
}


